import React, { useCallback, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import howler from 'howler';

const SoundTrigger = ({ src, isPlaying, getTime, loading, offset }) => {
  const [isMuted, setIsMuted] = useState(false);
  const [sound, setSound] = useState(/** @type Howl */undefined);


  useEffect(() => {
    loading((prevState) => (prevState ? prevState + 1 : 1));
    setSound(/** @type Howl */ new howler.Howl({
      src: [`assets/${src}.mp3`],
      autoUnlock: true,
      preload: true,
      onload: (a) => { loading((prevState) => prevState - 1); },
      onloaderror: (e) => { console.error(e); },
      onplayerror: (e) => { console.error(e); }
    }));
  }, [src]);

  useEffect(() => {
    if (!sound || offset === 0) { return; }
    sound.seek(offset);
  }, [sound, offset]);


  useEffect(() => {
    if (!getTime || !sound) { return undefined; }

    const t = setInterval(() => {
      getTime(sound.seek() || 0);
    }, 100);

    return () => {
      clearInterval(t);
    };
  }, [sound]);


  useEffect(() => {
    if (!sound) { return; }
    if (isPlaying) {
      sound.play();
    } else {
      sound.pause();
    }
  }, [sound, isPlaying]);


  const onClick = useCallback(() => {
    console.log(src, sound.seek());
  }, [sound]);

  const onMute = useCallback((e) => {
    setIsMuted(e.target.checked);
    sound.mute(e.target.checked);
  }, [sound]);


  return (
    <div>
      <button type="button" onClick={onClick}>
        get time {src}
      </button>

      <input
        id={`mute_${src}`}
        type="checkbox"
        checked={isMuted}
        onChange={onMute}
      />
      <label htmlFor={`mute_${src}`}>
        mute
      </label>
    </div>
  );
};

SoundTrigger.propTypes = {
  src: PropTypes.string.isRequired,
  isPlaying: PropTypes.bool.isRequired,
  offset: PropTypes.number,
  getTime: PropTypes.func,
  loading: PropTypes.func.isRequired
};
SoundTrigger.defaultProps = {
  offset: 0,
  getTime: undefined
};

export {
  SoundTrigger
};
