import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import howler from 'howler';
import { useSound } from '../utils/hooks';

const SoundStream = ({ src, isPlaying, getTime }) => {
  const sound = useSound(src);

  useEffect(() => {
    if (!sound) { return undefined; }

    if (isPlaying) {
      sound.play();
      const t = setInterval(() => {
        getTime(sound.seek() || 0);
      }, 100);

      return () => {
        clearInterval(t);
      };
    } else {
      sound.pause();
    }
    return undefined;
  }, [isPlaying, sound]);

  return (
    <div />
  );
};

SoundStream.propTypes = {
  src: PropTypes.string.isRequired,
  isPlaying: PropTypes.bool
};

SoundStream.defaultProps = {
  isPlaying: true
};

export {
  SoundStream
};
