import React, { useCallback, useEffect, useState } from 'react';
import { TimeDisplay } from '../TimeDisplay';
import { SoundStream } from './SoundStream';
import { useSound } from '../utils/hooks';


const playlist = [
  'speaker1',
  'speaker2',
  'speaker3',
  'speaker4',
  'speaker5',
  'speakerAll'
];

const PlayerStream = () => {
  const [src, setSrc] = useState('speakerAll');
  const [isPlaying, setIsPlaying] = useState(false);
  const [/* blank */, time, loading] = useSound(src, isPlaying);


  const Controls = useCallback(() => {
    if (loading === undefined) { return <div />; }
    if (loading) { return <>Buffering...</>; }
    return (
      <button
        type="button"
        onClick={() => {
          setIsPlaying((prevState) => !prevState);
        }}
      >
        {isPlaying ? 'Pause' : 'Play' }
      </button>
    );
  }, [loading]);


  const stationButton = useCallback((value) => {
    return (
      <div key={value}>
        <input
          type="radio"
          id={value}
          name="src"
          checked={src === value}
          onChange={(e) => {
            console.log('handleRadioButton', e.target.value);
            setSrc(e.target.value);
          }}
          value={value}
        />
        <label htmlFor={value}>
          {value}
        </label>
        <br />
        <br />
      </div>
    );
  }, [src]);

  return (
    <>
      <h1>Stream</h1>
      {playlist.map((station) => {
        return stationButton(station);
      })}

      <Controls />
      <TimeDisplay time={time} />
    </>
  );
};

PlayerStream.propTypes = {};
PlayerStream.defaultProps = {};

export { PlayerStream };
