import React, { useCallback, useEffect, useState } from 'react';
import { SoundTrigger } from './SoundTrigger';
import { TimeDisplay } from '../TimeDisplay';

const PlayerDownload = () => {
  const [loading, setLoading] = useState(undefined);
  const [offset, setOffset] = useState(0);
  const [time, setTime] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);

  const onClick = useCallback(() => {
    setIsPlaying((prevState) => !prevState);
  }, []);

  useEffect(() => {
    setOffset(time);
  }, [isPlaying]);

  const Controls = useCallback(() => {
    if (loading === undefined || loading !== 0) { return <>Loading...</>; }
    return (
      <button type="button" onClick={onClick} disabled={loading !== 0}>
        {isPlaying ? 'Pause' : 'Play' }
      </button>
    );
  }, [loading]);


  return (
    <>
      <h1>Download</h1>
      <SoundTrigger
        src="speaker1"
        getTime={setTime}
        isPlaying={isPlaying}
        loading={setLoading}
        offset={offset}
      />
      <SoundTrigger src="speaker2" isPlaying={isPlaying} loading={setLoading} offset={offset} />
      <SoundTrigger src="speaker3" isPlaying={isPlaying} loading={setLoading} offset={offset} />
      <SoundTrigger src="speaker4" isPlaying={isPlaying} loading={setLoading} offset={offset} />
      <SoundTrigger src="speaker5" isPlaying={isPlaying} loading={setLoading} offset={offset} />
      <Controls />
      <TimeDisplay time={time} />
    </>
  );
};

PlayerDownload.propTypes = {};
PlayerDownload.defaultProps = {};

export { PlayerDownload };
